export function detectExtension(fileName, url): any {
    const pathLike = fileName || url;
    if (!pathLike) {
        return 'unknown';
    }

    const extension = pathLike.split('.').pop().toLowerCase();
    const extensionsToTypes = {
        pdf: 'pdf',
        jpg: 'image',
        jpeg: 'image',
        heic: 'image',
        bmp: 'image',
        png: 'image',
        gif: 'image',
        svg: 'image',
        svgz: 'image',
        ep: 'image',
        eps: 'image',
        avi: 'video',
        flv: 'video',
        h264: 'video',
        mov: 'video',
        mp4: 'video',
        mwv: 'video',
        mkv: 'video',
        mp3: 'audio',
        wav: 'audio',
        wma: 'audio',
        ogg: 'audio',
        txt: 'text',
        json: 'text',
        html: 'text',
        xml: 'text',
        doc: 'office',
        docx: 'office',
        ppt: 'office',
        xls: 'office',
        xlsx: 'office',
        odt: 'office',
        ods: 'office',
        odp: 'office',
        dot: 'office',
        dotx: 'office',
        docm: 'office', // not supported https://example-files.online-convert.com/document/docm/example.docm
        dotm: 'office', // not yet tested
        pages: 'office', // not supported https://example-files.online-convert.com/document/pages/example.pages
        pptx: 'office',
        csv: 'office', // not supported https://file-examples-com.github.io/uploads/2017/02/file_example_CSV_5000.csv
        numbers: 'office', // not supported https://example-files.online-convert.com/spreadsheet/numbers/example.numbers
        pot: 'office', // not tested
        pps: 'office',
        ppsx: 'office',
        pptm: 'office', // not supported https://israeltekahun.com/pptm.pptm
        potx: 'office', // not supported https://israeltekahun.com/potx.potx
        potm: 'office', // not supported https://israeltekahun.com/potm.potm
        ppam: 'office', // could not find file
        ppsm: 'office', // not supported https://israeltekahun.com/ppsm.ppsm
        sldx: 'office', // not tested
        sldm: 'office', // not tested
        key: 'office', // not tested
    };

    return extensionsToTypes[extension] || 'unknown';
}
